body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.square.winning {
  background: rgb(255, 168, 168)
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.bold-selected-item {
  font-weight: bold;
}

.square.dedault-background {
  background: #fff;
}

.square.highlight-selected {
  background: rgb(185, 255, 212);
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 50px;
  padding: 20px;
  margin: auto;
}

.grid-board-game {
  grid-column: 1 / 2;
  border-left: rgb(20, 20, 20);
  border-width: 5px;
}

.grid-game-info {
  border-left: rgb(20, 20, 20);
  border-width: 5px;
  font-size: 20px;
}

.button {
  position: relative;
  background-color: #7a7a7a;
  border: none;
  font-size: 20px;
  color: #FFFFFF;
  padding: auto;
  width: 150px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  border-radius: 15px;
  margin: 10px;
  height: 50px;
}

.i01 {
  background-color: #cfcfcf;
  width: 300px;
  font-size: 15px;
  border-radius: 5px;
  color: #242424;
  height: 35px;
}

.i02 {
  width: 50px;
  height: 40px;
  border-radius: 10px;
  font-size: 15px;
}

.i01:hover {
  background-color: #252525;
  color: #ffffff;
}

.button:after {
  content: "";
  background: #afafaf;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px!important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s
}

.button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s
}

.button:hover {background-color: #313131}

.input {
  width: 50px;
  font-size: 15px;
  height: 30px;
}

